import { Message, MessageBox } from 'element-ui'
export default {
  checkResponseError (resp) {
    if (!resp) throw new Error('')
    if (!resp.data) throw new Error('')
    if (resp.data.timeout) throw new Error('')
    if (resp.data.errCode) throw new Error(resp.data.errMsg || '')
  },
  alertError (e) {
    console.error(e)
    MessageBox(e.message, '', {
      closeOnClickModal: false,
      customClass: 'big-zindex',
      confirmButtonText: ''
    })
  },
  popError (e) {
    console.error(e)
    Message({
      customClass: 'big-zindex',
      showClose: true,
      message: e.message,
      type: 'error'
    })
  }
}



