import Vue from 'vue'
import utils from 'ecp.utils'

import ProcessGraphDialog from '../components/ProcessGraph/ProcessGraphDialog.vue'
import ProcessGraphCanvas from '../components/ProcessGraph/ProcessGraphCanvas.vue'

import deepMerge from '../utils/deepMerge'

const Dialog = Vue.extend(ProcessGraphDialog)
const Canvas = Vue.extend(ProcessGraphCanvas)

/**
 * 
 */
let _instance

/**
 * 
 */
const defaultOptions = () => {
  return {
    /**
     * id
     */
    taskId: undefined,
    /**
     * 
     */
    processDefName: undefined,
    /**
     * id
     */
    processDefId: undefined,
    /**
     * id
     */
    processInstId: undefined,
    /**
     * id
     */
    containerId: undefined,
    fullscreen: true,
    height: null,
    width: null
  }
}

/**
 * 
 * @param {} options
 */
const ProcessGraph = (options) => {
  if (!checkOptions(options)) {
    Vue.prototype.$alert('ID', '')
    return
  }
  // 
  showGraph(deepMerge(defaultOptions(), options))
}

/**
 * dialog
 * @param {} options
 */
const showGraph = (options) => {
  // 
  if (!options.containerId) {
    initDialog(options)
    document.body.appendChild(_instance.$el)
    Vue.nextTick(() => {
      _instance.visible = true
    })
  } else {
    // 
    initCanvas(options)
  }
}

/**
 * .
 * @param {} options
 */
const initDialog = (options) => {
  var el
  var x = document.getElementById('processGraph')
  if (x) el = x
  else el = document.createElement('div')

  _instance = new Dialog({
    el: el,
    data: {
      options: options
    }
  })
}

/**
 * .
 * @param {} options
 */
const initCanvas = (options) => {
  var el = document.createElement('div')
  document.getElementById(options.containerId).appendChild(el)
  _instance = new Canvas({
    el: el,
    // el: document.getElementById(options.containerId),
    propsData: {
      options: options,
      containerId: options.containerId
    }
  })
}

/**
 * .
 */
const checkOptions = (options) => {
  if (utils.isNotEmpty(options.taskId) ||
  utils.isNotEmpty(options.processDefName) ||
  utils.isNotEmpty(options.processDefId) ||
  utils.isNotEmpty(options.processInstId)) return true
  return false
}

export default ProcessGraph



