import Events from './events.js'

export default function Options () {
  var events = new Events()
  return {
    ...events,
    /**
     * id
     */
    processDefName: undefined,
    /**
     * 
     */
    action: undefined,
    /**
     * action=start
     */
    completeFirstTask: false,
    /**
     * id
     */
    businessKey: undefined,
    /**
     * id
     */
    taskId: undefined,
    /**
     * 
     */
    bizDataModel: undefined,
    /**
     * 
     */
    taskVariables: undefined,
    /**
     * 
     */
    autoCloseFormPage: true
  }
}



