import Vue from 'vue'
import utils from 'ecp.utils'
import ProcessTraceTable from '../components/processTrace/pc/ProcessTraceTable.vue'
import deepMerge from '../utils/deepMerge'
import ErrorUtil from '../utils/ErrorUtil'
import store from '../components/processTrace/store/index'

const Constructor = Vue.extend(ProcessTraceTable)

/**
 * 
 */
let _instance

/**
 * 
 */
const defaultOptions = () => {
  return {
    taskId: undefined,
    processInstId: undefined,
    filterTerminateData: true,
    fullscreen: false
  }
}

/**
 * 
 * @param {} options
 */
const ProcessTrace = (options) => {
  if (!checkOptions(options)) {
    ErrorUtil.alertError(new Error('taskId  processInstId '))
    return
  }
  // 
  showTable(deepMerge(defaultOptions(), options))
}

/**
 * dialog
 * @param {} options
 */
const showTable = (options) => {
  // 
  initInstance(options)

  document.body.appendChild(_instance.$el)

  Vue.nextTick(() => {
    _instance.visible = true
  })
}

/**
 * .
 * @param {} options
 */
const initInstance = (options) => {
  var el
  var x = document.getElementById('processTrace')
  if (x) el = x
  else el = document.createElement('div')

  _instance = new Constructor({
    el: el,
    store,
    data: {
      options: options
    }
  })
}

/**
 * .
 */
const checkOptions = (options) => {
  if (utils.isNotEmpty(options.taskId) || utils.isNotEmpty(options.processInstId)) return true
  return false
}

export default ProcessTrace



